<template>
  <v-container grid-list-xs>
    <v-overlay :value="overlay">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
    </v-overlay>
    <v-alert type="primary">
      <center><b>{{ $store.state.user.name }}</b></center>
    </v-alert>
    <v-alert
      dense
      text
      type="success"
      v-if="regStatus === 'Active'"
    >
      {{ $t("App.hardcoded-texts.Your registration is Active and Confirmed") }}
    </v-alert>
    <v-alert
      dense
      text
      type="warning"
      v-else-if="regStatus === 'Pending'"
    >
      {{ $t("App.hardcoded-texts.Your Registration is Pending Waiting Approval From Registration Offices") }}
    </v-alert>
    <v-alert
      dense
      outlined
      type="error"
      v-else-if="regStatus === 'Inactive' && regStatusCode !== 'new'"
    >
      {{ $t("App.hardcoded-texts.Your registration has expired, kindly renew") }}
    </v-alert>
    <v-alert
      dense
      outlined
      type="error"
      v-else-if="regStatusCode === 'new'"
    >
      {{ $t("App.hardcoded-texts.Your application has been accepted, kindly click the register button below to complete registration") }}
    </v-alert>
    <v-card
      class="md ma-2 content"
      shaped
    >
      <v-card-title
        prepend-icon="mdi-list"
        class="primary white--text subtitle font-weight-bold text-uppercase"
        dark
      >
        <v-icon dark>mdi-list</v-icon>
        {{ $t("App.hardcoded-texts.Choose What You Want To From Below") }}
      </v-card-title>
      <v-card-text>
        <v-list v-if="regStatusCode === 'new'">
          <v-list-item
            :to="`/custom/registration-renewal?path=/itsf&practitioner=` + practitioner"
          >
            <v-list-item-title class="font-weight-bold">{{ $t("App.hardcoded-texts.Register") }}</v-list-item-title>
          </v-list-item>
        </v-list>
        <v-list v-else>
          <v-list-item
            :to="`/custom/registration-renewal?path=/itsf&practitioner=` + practitioner"
          >
            <v-list-item-title class="font-weight-bold">{{ $t("App.hardcoded-texts.Renew Registration") }}</v-list-item-title>
          </v-list-item>
        </v-list>
        <v-list>
          <v-list-item
            :to="`/resource/view/student/` + practitioner"
          >
            <v-list-item-title class="font-weight-bold">{{ $t("App.hardcoded-texts.View Profile") }}</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-card-text>
    </v-card>
    <br>
    <b>Badili Lugha</b> <v-radio-group v-model="language" row @change="handleMenuItemClick">
      <v-radio
        label="Kiswahili"
        value="sw"
      ></v-radio>
      <v-radio
        label="English"
        value="en"
      ></v-radio>
    </v-radio-group>
    <v-row justify="center" v-if="payments.required > 0 && payments.pending === 0">
      <v-col cols="12" md="12">
        <v-card outlined color="#e8f5e9" class="pa-6 elevation-3">
          <v-row align="center">
            <v-col cols="12" md="2" class="text-center">
              <v-icon size="48" color="green darken-2">mdi-check-decagram</v-icon>
            </v-col>
            <v-col cols="12" md="10">
              <h2 class="font-weight-bold mb-2 success--text">Payment Completed!</h2>
              <p class="subtitle-1">
                Congratulations! You have successfully completed payment of fees.
              </p>
            </v-col>
          </v-row>

          <v-row>
            <v-col cols="12">
              <v-card class="pa-4" outlined>
                <h3 class="font-weight-medium mb-3">Payment Summary</h3>
                <v-row>
                  <v-col cols="12" sm="4">
                    <v-chip outlined color="blue" text-color="blue darken-2" class="ma-1" label>
                      Required: <strong class="ml-2">TZS {{ payments.required.toLocaleString() }}</strong>
                    </v-chip>
                  </v-col>
                  <v-col cols="12" sm="4">
                    <v-chip outlined color="green" text-color="green darken-2" class="ma-1" label>
                      Paid: <strong class="ml-2">TZS {{ payments.paid.toLocaleString() }}</strong>
                    </v-chip>
                  </v-col>
                  <v-col cols="12" sm="4">
                    <v-chip outlined color="grey lighten-1" text-color="grey darken-3" class="ma-1" label>
                      Pending: <strong class="ml-2">TZS {{ payments.pending.toLocaleString() }}</strong>
                    </v-chip>
                  </v-col>
                </v-row>
              </v-card>
            </v-col>
          </v-row>

          <v-alert
            type="info"
            class="mt-6"
            colored-border
            border="left"
            elevation="2"
            color="blue lighten-5"
          >
            <v-icon left color="blue darken-2">mdi-information</v-icon>
            You may now proceed to the next step of your registration.
          </v-alert>
        </v-card>
      </v-col>
    </v-row>
    <v-row justify="center" v-if="payments.pending > 0">
      <v-col cols="12" md="12">
        <v-card outlined color="#fffbe6" class="pa-6 elevation-3">
          <v-row align="center">
            <v-col cols="12" md="2" class="text-center">
              <v-icon size="48" color="orange darken-2">mdi-check-circle</v-icon>
            </v-col>
            <v-col cols="12" md="10" v-if="payments.paid === 0">
              <h2 class="font-weight-bold mb-2">Action Required!</h2>
              <p class="subtitle-1">
                <strong>First step of registration is successful!</strong> To finish your registration, please pay fees by visiting any CRDB branch or authorized CRDB agent or SIM Banking App using below control number:
              </p>
            </v-col>
            <v-col cols="12" md="10" v-else-if="payments.pending > 0">
              <h2 class="font-weight-bold mb-2">Action Required!</h2>
              <p class="subtitle-1">
                Please complete payment of fees by visiting any CRDB branch or authorized CRDB agent or SIM Banking App using below control number:
              </p>
            </v-col>
          </v-row>

          <v-divider class="my-4"></v-divider>

          <v-row>
            <v-col cols="12">
              <v-alert
                type="info"
                border="left"
                colored-border
                elevation="2"
                color="blue lighten-5"
                class="mb-4"
              >
                <h3 class="font-weight-bold mb-2">Your Payment Control Number</h3>
                <p class="display-1 font-mono primary--text">{{ controlNumber }}</p>
              </v-alert>
            </v-col>
          </v-row>

          <v-row>
            <v-col cols="12">
              <v-card class="pa-4" outlined>
                <h3 class="font-weight-medium mb-3">Payment Status</h3>
                <v-row>
                  <v-col cols="12" sm="4">
                    <v-chip outlined color="blue" text-color="blue darken-2" class="ma-1" label>
                      Required: <strong class="ml-2">TZS {{ payments.required.toLocaleString() }}</strong>
                    </v-chip>
                  </v-col>
                  <v-col cols="12" sm="4">
                    <v-chip outlined color="green" text-color="green darken-2" class="ma-1" label>
                      Paid: <strong class="ml-2">TZS {{ payments.paid.toLocaleString() }}</strong>
                    </v-chip>
                  </v-col>
                  <v-col cols="12" sm="4">
                    <v-chip outlined color="orange" text-color="orange darken-2" class="ma-1" label>
                      Pending: <strong class="ml-2">TZS {{ payments.pending.toLocaleString() }}</strong>
                    </v-chip>
                  </v-col>
                </v-row>
              </v-card>
            </v-col>
          </v-row>

          <v-alert
            type="warning"
            class="mt-6"
            colored-border
            border="left"
            elevation="2"
            color="amber lighten-4"
            v-if="payments.pending > 0"
          >
            <v-icon left color="orange darken-3">mdi-alert-circle</v-icon>
            Please make sure to complete the payment to proceed with the next steps of registration.
          </v-alert>
        </v-card>
      </v-col>
    </v-row>
    <v-card 
        class="mx-auto"
        outlined
        v-if="members.length"
      >
        <v-card-title class="primary darken-1 white--text text-uppercase font-weight-bold">
          {{ $t("App.hardcoded-texts.Family Members Registration Status") }}
        </v-card-title>
        <v-alert
          dense
          text
          type="success"
        >
          {{ $t("App.hardcoded-texts.Contact registration office if you dont recognise any of the family members") }}
        </v-alert>
        <v-data-table
          :headers="membersHeaders"
          :items="members"
          item-key="name"
          class="elevation-1"
          :search="searchMember"
        >
          <template v-slot:top>
            <v-text-field
              v-model="searchMember"
              label="Search"
              class="mx-4"
            ></v-text-field>
          </template>
          <template v-slot:item.reg_status="{ item }">
            <v-chip
              :color="item.reg_status_color"
              dark
            >
              {{ item.reg_status }}
            </v-chip>
          </template>
          <template v-slot:item.action="{ item }">
            <v-btn v-if="!item.reg_status_code && regStatusCode != 'expired'" color="success" @click="$router.push('/custom/registration-renewal?path=%2Fitsf&practitioner=' + item.id)">{{$t("App.hardcoded-texts.Renew Member")}}</v-btn>
          </template>
        </v-data-table>
      </v-card>
  </v-container>
</template>
<script>
import {loadLanguage} from '../../i18n'
export default {
  data: () => ({
    overlay: false,
    practitioner: "",
    regStatus: "",
    regStatusCode: "",
    language: "en",
    searchMember: "",
    members: [],
    controlNumber: "",
    payments: {},
  }),
  computed: {
    isMobile() {
      if(screen.width > 760) {
        return false
      }
      return true
    },
    membersHeaders() {
      let headers = [{
        text: this.$t("App.hardcoded-texts.Name"),
        value: "name"
      }, {
        text: this.$t("App.hardcoded-texts.Registration Number"),
        value: "regNum"
      }, {
        text: this.$t("App.hardcoded-texts.Fees Category"),
        value: "feecat"
      }, {
        text: this.$t("App.hardcoded-texts.Required Fee"),
        value: "requiredfee"
      }, {
        text: this.$t("App.hardcoded-texts.Paid Fee"),
        value: "paidfee"
      }, {
        text: this.$t("App.hardcoded-texts.Pending Fee"),
        value: "pendingfee"
      }, {
        text: this.$t("App.hardcoded-texts.Registration Status"),
        value: "reg_status"
      }]
      if(this.regStatusCode) {
        headers.push({
          text: this.$t("App.hardcoded-texts.Action"),
          value: "action"
        })
      }
      return headers
    }
  },
  methods: {
    handleMenuItemClick () {
      loadLanguage(this.language)
      localStorage.setItem('activeLocale', this.language)
    },
    getActiveFamilyMembers() {
      this.overlay = true
      fetch("/site/student/getFamilyMembers/" + this.practitioner).then((response) => {
        if(response.ok) {
          response.json().then((response) => {
            this.members = response
            if(response.length) {
              let promises = []
              for(let member of this.members) {
                promises.push(new Promise((resolve) => {
                  fetch("/site/student/latestRegistration/" + member.id).then((response) => {
                    resolve()
                    if(response.ok) {
                      this.$set(member, "reg_status", this.$t("App.hardcoded-texts.Expired"))
                      this.$set(member, "reg_status_color", "error")
                    } else {
                      response.json().then((response) => {
                        if(response.regStatus === "pending") {
                          this.$set(member, "reg_status", this.$t("App.hardcoded-texts.Pending Approval"))
                          this.$set(member, "reg_status_code", response.regStatus)
                          this.$set(member, "reg_status_color", "warning")
                        } else if(response.regStatus === "approved") {
                          this.$set(member, "reg_status", this.$t("App.hardcoded-texts.Active"))
                          this.$set(member, "reg_status_color", "success")
                          this.$set(member, "reg_status_code", response.regStatus)
                        } else if(response.regStatus === "rejected") {
                          this.$set(member, "reg_status", this.$t("App.hardcoded-texts.Rejected"))
                          this.$set(member, "reg_status_color", "error")
                          this.$set(member, "reg_status_code", response.regStatus)
                        }
                      })
                    }
                  }).catch(() => {
                    resolve()
                  })
                }))
              }
              Promise.all(promises).then(() => {
                this.overlay = false
              })
            } else {
              this.overlay = false
            }
          })
        } else {
          this.overlay = false
          this.$store.commit("setMessage", {
            type: "error",
            text: "An error has occured, try again later",
          });
        }
      })
    }
  },
  created() {
    this.language = this.$i18n.locale
    if(this.isMobile) {
      this.$store.state.minidrawer = true
    }
    if(this.$store.state.user.obj.resource) {
      let pract = this.$store.state.user.obj.resource.extension.find((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference"
      })
      if(pract && pract.valueReference && pract.valueReference.reference) {
        this.practitioner = pract.valueReference.reference.split("/")[1]
        fetch("/site/student/registrationStatus/" + this.practitioner).then((response) => {
          response.json().then((response) => {
            if(response.payments) {
              this.payments = response.payments
            }
            if(response.controlNumber && response.controlNumber.data.length) {
              this.controlNumber = response.controlNumber.data[0].hijiridate + response.controlNumber.data[0].id
            }
            this.regStatus = response.status
            this.regStatusCode = response.statusCode
          })
        })
        this.getActiveFamilyMembers()
      } else {
        this.$router.go(-1)
      }
    } else {
      this.$router.push("/")
    }
  }
}
</script>